import { FormGroup, ValidationErrors, ValidatorFn } from '@angular/forms';

//TODO: Delete
export function RequiredConditionValidator(controlName: string): ValidatorFn {
  return (formGroup: FormGroup): ValidationErrors | null => {
    const categoryControl = formGroup.controls['category'];
    if (categoryControl.value) {
      switch (controlName) {
        case 'fit':
          {
            const isFitRequired = categoryControl.value.wardrobe_additional_fields?.fit_id;
            return isFitRequired && formGroup.controls[controlName].value === null
              ? { required: { value: 'required' } }
              : null;
          }
          break;
        case 'size':
          {
            const isSizeRequired = categoryControl.value.wardrobe_additional_fields?.size_id;
            return isSizeRequired && formGroup.controls[controlName].value === null
              ? { required: { value: 'required' } }
              : null;
          }
          break;
      }
    }
  };
}
